@import url(https://fonts.googleapis.com/css?family=Roboto:500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 100%;
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.main-wrapper {
  background: url(/meet-app/static/media/wm_back.829ef3c3.svg);
  min-height: 100vh;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.top-bar-wrapper {
  position: fixed;
  top: 0;
  width: 100%;
  height: 55px;
  background: white;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  transition: all 0.3s ease;
  z-index: 1000 ;
  opacity: .99;
}

.top-bar-wrapper.expand {
  position: fixed;
  height: 100px;
  padding: 0;
}

.top-bar {
  display: flex;
  flex-direction: row;
  width: 95%;
  margin: 0 auto;
}

.event-list-wrapper {
  padding-top: 55px;
  max-width: 50rem;
  min-width: 55%;
  margin: 0 auto;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.WelcomeScreen {
  position: fixed;
  width: 100%;
  height: 100%;
}

.welcome-background {
  width: 100%;
  height: 100%;
}

.welcome-content-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 50%;
  height: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: white;
  opacity: 0.9;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}

.fadeOut {
  opacity: 0;
  width: 50%;
  height: 5px;
  transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s;
}

.fadeIn {
  opacity: 1;
  width: 50%;
  height: 50%;
  transition: width 0s, height 2s, opacity 1s 0.5s;
}

.login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}

.google-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}

.google-btn:hover {
  box-shadow: 0 0 6px #4285f4;
}

.google-btn:active {
  background: #1669f2;
}
.google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: #fff;
}
.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}

.btn-text {
  float: right;
  color: #fff;
  font-size: 14px;
  letter-spacing: 0.2px;
  font-family: 'Roboto';
  background-color: #4285f4;
  width: 80%;
  height: 100%;
  text-align: center;
  margin: 0 !important;
}

.priv-link {
  color: #4285f4;
  text-decoration: none;
}

@media (max-width: 770px) {
  .welcome-content-wrapper {
    width: 75%;
  }
}

@media (max-width: 430px) {
  .welcome-content-wrapper {
    width: 95%;
  }
}

html {
  height: 100%;
}

.App {
  text-align: center;
}

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

.display-none {
  display: none;
}
.showSuggestions {
  display: block;
}

input[type='text'],
input[type='number'] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  height: 38px;
}

.city {
  width: 250px;
}

.CitySearch {
  padding-top: 10px;
}

.CitySearch p {
  text-align: left;
}

.suggestions {
  width: 250px;
  position: absolute;
  z-index: 2;
  align-self: center;
  margin: 0;
  top: 70px;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #bcdcdb;
}

.event {
  background: white;
  padding: 10px 10px 35px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 10px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
  position: relative;
  z-index: 1;
}

.event p {
  padding-bottom: 10px;
}

.show-button {
  position: absolute;
  font-size: 1rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  bottom: 5px;
  padding: 5px 8px;
  border-radius: 10px;
  outline: none;
  cursor: pointer;
  background: white;
  transition: top ease 0.5s;
}

.show-button:hover {
  -webkit-transform: translate(-50%, -3px);
          transform: translate(-50%, -3px);
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.numberOfEvents {
  padding-top: 10px;
  padding-left: 50px;
}

.numberOfEvents p {
  text-align: left;
}

.event-num-wrapper {
  text-align: left;
}

.event-input {
  text-align: left;
  width: 200px;
}

.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}

.info-alert {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
}

.offline-alert {
  position: absolute;
  top: -5px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}

.error-alert {
  position: absolute;
}

.top-info {
  padding-top: 5px;
  width: 95%;
  margin: 0 auto;
}

.top-title {
  font-size: 1.5rem;
}

.top-bar {
  visibility: hidden;
  opacity: 0;
}

.top-bar.top-bar-show {
  visibility: visible;
  opacity: 1;
  transition: all 0.5s ease;
}

.top-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.top-col {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.meet-logo-wrap {
  height: 55px;
  width: 55px;
}

.meet-logo-wrap img {
  width: 100%;
  height: auto;
}

.input-expand {
  position: absolute;
  bottom: -18px;
  left: 50%;
  right: 50%;
  margin-left: auto;
  margin-right: auto;
  height: 40px;
  width: 40px;
  border: black solid 1px;
  background-repeat: no-repeat;
  border-radius: 100px;
  background-color: white;
  z-index: 1000;
}

.data-vis-wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.data-vis-wrapper .recharts-responsive-container {
  min-width: 100%;
  display: flex;
}
.recharts-surface {
  overflow: visible;
}

.login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  padding: 20px;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}

.google-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
.google-btn:hover {
  box-shadow: 0 0 6px #4285f4;
}
.google-btn:active {
  background: #1669f2;
}
.google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: #fff;
}
.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}
.btn-text {
  float: right;
  margin: 11px 11px 0 0;
  color: #fff;
