.chart-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.genre-chart {
  padding-left: 100px;
  width: 50%;
  height: 100%;

}

.scatter-chart {
  padding-right: 75px;
  width: 50%;
  height: 100%;
}

.recharts-responsive-container {
  padding-top: 60px;
}

text.recharts-pie-label-text {
  font-size: 0.9rem;
}

tspan {
  fill: white;
}

@media only screen and (min-width: 992px) {
  .data-vis-wrapper {
    flex-direction: row;
  }
  .data-vis-wrapper .recharts-responsive-container {
    flex-basis: 50%;
    max-width: 50%;
    min-width: auto;
    margin: 0 auto;
  }
}

@media (max-width: 770px) {
  .chart-wrapper {
    justify-content: space-between;
  }

  .genre-chart {
    padding-left: 0px;

  }

  .scatter-chart {
    padding-right: 0px;

  }
}


@media (max-width: 430px) {
  .numberOfEvents p,
  .CitySearch p {
    font-size: 0.75rem;
  }
  .event-input {
    width: 50px;
  }
  .city {
    width: 150px;
  }

  .genre-chart {
    padding-left: 20px;

  }

  .chart-wrapper {
    flex-direction: column;
    width: 100%;
    z-index: 50000;
  }
  .scatter-chart {
    width: 100%;
    padding-right: 15px;

  }
}

/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #29d;

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #29d, 0 0 5px #29d;
  opacity: 1;

  -webkit-transform: rotate(3deg) translate(0px, -4px);
  transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 15px;
  right: 15px;
}

#nprogress .spinner-icon {
  width: 18px;
  height: 18px;
  box-sizing: border-box;

  border: solid 2px transparent;
  border-top-color: #29d;
  border-left-color: #29d;
  border-radius: 50%;

  -webkit-animation: nprogress-spinner 400ms linear infinite;
  animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}

@-webkit-keyframes nprogress-spinner {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes nprogress-spinner {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

